<template>
  <div>
    <breadcrumb :breadcrumbItemList="['', '']"></breadcrumb>
    <el-card>
      <el-alert
        title=""
        type="warning"
        show-icon
        :closable="false"
      ></el-alert>
      <!---->

      <el-row class="row">
        <el-col>
          <span></span>
          <el-cascader
            v-model="selectKeys"
            :options="allCategoryList"
            :props="cascaderProps"
            @change="handleChange"
            :clearable="true"
          ></el-cascader>
        </el-col>
      </el-row>
      <!--tab-->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many" :disabled="isTabClickable">
          <el-button
            type="primary"
            @click="showAddParamsDialog"
            :disabled="isTabClickable"
            ></el-button
          >
          <!--table-->
          <el-table :data="manyParamsList" stripe border>
            <!---->
            <el-table-column type="expand">
              <template v-slot:default="scope">
                <!---->
                <el-tag
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  @close="deleteAttr(scope.row, i)"
                  >{{ item }}</el-tag
                >
                <!---->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template v-slot:default="slot">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditParamsDialog(slot.row)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="showDeleteParamsMessageBox(slot.row)"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--tab-->
        <el-tab-pane label="" name="only" :disabled="isTabClickable">
          <el-button
            type="primary"
            @click="showAddParamsDialog"
            :disabled="isTabClickable"
            ></el-button
          >
          <!--table-->
          <el-table :data="onlyParamsList" stripe border>
            <!---->
            <!---->
            <el-table-column type="expand">
              <template v-slot:default="scope">
                <!---->
                <el-tag
                  closable
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  @close="deleteAttr(scope.row, i)"
                  >{{ item }}</el-tag
                >
                <!---->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label=""
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="">
              <template v-slot:default="slot">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditParamsDialog(slot.row)"
                ></el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="showDeleteParamsMessageBox(slot.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--/-->
    <popup
      :title="activeName === 'many' ? '' : ''"
      :dialogVisible="addParamsDialogVisible"
      width="50%"
      @save="saveParams"
      @handleClose="handleAddClose"
    >
      <!---->
      <el-form
        :model="addParamsForm"
        :rules="addParamsRules"
        ref="addParamsForm"
        label-width="100px"
      >
        <el-form-item label="" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
    </popup>

    <!--/-->
    <popup
      :title="activeName === 'many' ? '' : ''"
      :dialogVisible="editParamsDialogVisible"
      width="50%"
      @handleClose="handleEditClose"
      @save="editParams"
    >
      <!---->
      <el-form
        :model="editParamsForm"
        :rules="editParamsRules"
        ref="editParamsForm"
        label-width="100px"
      >
        <el-form-item label="" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
    </popup>
  </div>
</template>

<script>
//
import Breadcrumb from "components/common/breadcrumb/Breadcrumb";
import Popup from "components/common/popup/Popup";
//
import {
  queryGoodsCategories,
  queryGoodsParams,
  addParams,
  deleteParams,
  queryParamsById,
  editParam,
} from "network/goodsParams";
export default {
  name: "GoodsParams",
  data() {
    return {
      allCategoryList: [],
      selectKeys: [],
      cascaderProps: {
        expandTrigger: "hover",
        label: "cat_name",
        value: "cat_id",
        children: "children",
      },
      activeName: "many",
      isTabClickable: true,
      queryGoodsParamsParams: {
        id: "",
        sel: "",
      },
      manyParamsList: [],
      onlyParamsList: [],
      addParamsDialogVisible: false,
      addParamsForm: {},
      addParamsRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      editParamsDialogVisible: false,
      editParamsForm: {},
      editParamsRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  components: {
    Breadcrumb,
    Popup,
  },
  created() {
    this.queryGoodsCategories({});
  },
  methods: {
    queryGoodsCategories(paramsObject) {
      queryGoodsCategories(paramsObject).then((res) => {
        res.meta.status === 200
          ? (() => {
              this.allCategoryList = res.data;
            })()
          : this.$message.error(res.meta.msg);
      });
    },
    queryGoodsParams(paramsObject) {
      queryGoodsParams(paramsObject).then((res) => {
        res.meta.status === 200
          ? (() => {
              //
              res.data.forEach((item) => {
                item.attr_vals =
                  item.attr_vals == [""] ? [] : item.attr_vals.split(",");
                item.inputVisible = false;
                item.inputValue = "";
              });

              //
              this.activeName === "many"
                ? (this.manyParamsList = res.data)
                : (this.onlyParamsList = res.data);
            })()
          : this.$message.error(res.meta.msg);
      });
    },
    //
    addParams(paramsObject) {
      //cateId
      this.addParamsForm.attr_id = this.cateId;
      //activeName
      this.addParamsForm.attr_sel = this.activeName;
      addParams(paramsObject).then((res) => {
        res.meta.status === 201
          ? (() => {
              this.addParamsDialogVisible = false;
              //
              this.queryGoodsParams(this.queryGoodsParamsParams);
            })()
          : this.$message.error(res.meta.msg);
      });
    },
    //
    saveParams() {
      this.addParams(this.addParamsForm);
    },
    //
    handleChange() {
      this.isTabClickable = this.selectKeys.length == 0 ? true : false;
      //  tab
      this.queryGoodsParamsParams.id = this.cateId;
      this.queryGoodsParamsParams.sel = this.activeName;
      this.queryGoodsParams(this.queryGoodsParamsParams);
    },
    //tab
    handleTabClick() {
      //  tab
      this.queryGoodsParamsParams.id = this.cateId;
      this.queryGoodsParamsParams.sel = this.activeName;
      this.queryGoodsParams(this.queryGoodsParamsParams);
    },
    showAddParamsDialog() {
      ///
      this.addParamsDialogVisible = true;
    },
    showDeleteParamsMessageBox(param) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          //cateId  attrid
          ///
          this.deleteParams(param);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    deleteParams(param) {
      deleteParams(param.cat_id, param.attr_id).then((res) => {
        res.meta.status === 200
          ? (() => {
              //
              this.queryGoodsParams(this.queryGoodsParamsParams);
            })()
          : this.$message.error(res.meta.msg);
      });
      //
      this.$message({
        type: "success",
        message: "!",
      });
    },
    showEditParamsDialog(param) {
      //
      this.editParamsDialogVisible = true;
      //
      const editParamsFormParams = {
        cat_id: param.cat_id,
        attr_id: param.attr_id,
        attr_sel: this.activeName,
        attr_vals: param.attr_vals,
      };
      queryParamsById(editParamsFormParams).then((res) => {
        this.editParamsForm = res.data;
      });
    },
    handleEditClose() {
      this.editParamsDialogVisible = false;
    },
    handleAddClose() {
      this.addParamsDialogVisible = false;
    },
    editParams() {
      //
      this.editParamsDialogVisible = false;
      //
      editParam(this.editParamsForm).then((res) => {
        //
        this.queryGoodsParams(this.queryGoodsParamsParams);
      });
    },

    ///
    handleInputConfirm(row) {
      let inputValue = row.inputValue;
      if (inputValue) {
        row.attr_vals.push(inputValue);
      }

      row.inputVisible = false;
      row.inputValue = "";

      //
      const editParamsFormParams = {
        cat_id: row.cat_id,
        attr_id: row.attr_id,
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(","),
      };

      editParam(editParamsFormParams).then((res) => {
        //
        this.queryGoodsParams(this.queryGoodsParamsParams);
      });
    },
    //Input
    showInput(row) {
      row.inputVisible = true;
      //
      //$nextTick 
      //taginputVisibletrue
      //
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    deleteAttr(param, i) {
      param.attr_vals.splice(i, 1);
      this.deleteParams(param);
    },
  },
  computed: {
    //0
    cateId() {
      return this.selectKeys.length > 0 ? this.selectKeys[2] : 0;
    },
  },
};
</script>

<style scoped>
.row {
  margin: 15px 0px;
}
.el-tag {
  margin-right: 10px;
  margin-bottom: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
